.services {
    width: 100%;
    background: url("../img/backgrond.png") no-repeat;
    padding-bottom: 80px;
}

.services__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 40px;
    margin-bottom: 40px;
}

.services__tittle {
    font-family: "Bodoni";
    font-weight: 400;
    opacity: 0.8;
    font-size: 28px;
    letter-spacing: 1.1px;
    font-variant: small-caps;
    color: #493a2f;
    margin: 0;
    margin-top: 5px;
    margin-bottom: 5px;
}

.services__img {
    width: 320px;
    height: 395px;
}

.slick-slide {
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
}

.services__block {
    position: absolute;
    height: 192px;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: rgba($color: #493a2f, $alpha: 0.85) ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: 1s;
}

.services__description {
    width: 227px;
    font-size: 24px;
    font-family: "Bondi";
    font-weight: 400;
    letter-spacing: 2.52px;
    margin: 0;
    z-index: 99;
    display: block;
    position: relative;
}

.btn__services {
    width: 193px;
    height: 49px;
    border-radius: 3px;
    border: 2px solid #c69c6d;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;
    letter-spacing: 0.34px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
    margin-top: 20px;
}

.btn__services:hover {
    background: #c69c6d;
    transition: 1s;
}

.btn__services--active {
    display: none;
}

.services__block--active {
    height: 100%;
    transition: 1s;
}

.services-block__list {
    color: #c69c6d;
    text-align-last: left;
    margin-top: 50px;
}

.sevices-block__item {
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: 400;
}

.services-block__border {
    width: 121px;
    height: 3px;
    background: #c69c6d;
    position: relative;
    display: block;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 30px;
}

.services__text {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.34px;
}

.sevices-block__item span{
    color: #ffffff;        
}

.btn__close {
    background-image: url("../img/botton_close.png");
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;
    width: 17px;
    height: 17px;
}



@media (min-width:1440px){
    .services {
        width: 100%;
        height: 995px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: url("../img/backgrond.png") no-repeat;
        padding-bottom: 0;
    }

    .services__wrap {
        width: 400px;
        height: 124px;
        margin-bottom: 60px;
        margin-top: 80px;
    }

    .services__tittle {
        font-size: 48px;
    }

    .services__border {
        width: 398px;
    }

    .services__tittle {
        opacity: 0.8;
    }

    .services-item {
        width: 1254px;
    }

    .services-item .slick-prev {
        left: -25px;
        background-image: url(../img/botton_left-pre.png);
        background-repeat: no-repeat;
        background-position: center;
        height: 29px;
        width: 26px;
        display: block;
    }

    .services-item .slick-next {
        right: -25px;
        background-image: url(../img/botton_right-next.png);
        background-repeat: no-repeat;
        background-position: center;
        height: 29px;
        width: 26px;
        display: block;
    }

    .slick-slide {
        position: relative;
    }

    .services__img {
        width: unset;
        height: unset;
    }

    .services__block {
        position: absolute;
        height: 192px;
        width: 95%;
        bottom: 0;
        left: 10px;
        background-color: rgba($color: #493a2f, $alpha: 0.85) ;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        transition: 1s;
    }

    .services__description {
        width: 227px;
        font-size: 28px;
        font-family: "Bondi";
        font-weight: 400;
        letter-spacing: 2.52px;
        margin: 0;
    }

    .btn__services {
        width: 193px;
        height: 49px;
        border-radius: 3px;
        border: 2px solid #c69c6d;
        font-weight: 700;
        text-decoration: none;
        text-transform: uppercase;
        color: #ffffff;
        letter-spacing: 0.34px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 1s;
        margin-top: 20px;
    }

    .btn__services:hover {
        background: #c69c6d;
        transition: 1s;
    }

    .btn__services--active {
        display: none;
    }

    .services__block--active {
        height: 100%;
        transition: 1s;
    }

    
}
