.inform {
    width: 100%;
    background-color: #493a2f;
    display: flex;
    align-items: center;
    justify-content: center;
}

.inform__tittle {
    width: 100%;
    color: #ffffff;
    font-size: 22px;
    letter-spacing: 1.51px;
    text-align: center;
    font-family: "Bodoni";
    font-weight: 400;
}

.inform__border {
    width: 220px;
    margin: 0 auto;
}

.inform__list {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-top: 30px;
}

.inform__item {
    height: 250px;
    width: 150px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    margin-left: 5px;
    margin-right: 5px;
}

@media (min-width: 1440px){
    .inform {
        width: 100%;
        height: 959px;
    }

    .inform__wrap {
        max-width: 1027px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .inform__tittle {
        font-size: 55px;
    }

    .inform__border {
        background-color: #c69c6d;
        width: 380px;
        margin-bottom: 100px;
    }

    .inform__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        list-style: none;
    }

    .inform__item {
        height: 250px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        margin-right: 80px;
        text-align: center;
    }

    .inform__ico {
        width: 66px;
        height: 66px;
    }

    .inform__description {
        font-size: 24px;
        font-family: "Bodoni";
        font-weight: 400;
        width: 213px;
    }

    .inform__bottom {
        width: 90px;
        background-color: #7a716c;
        height: 2px;
        position: relative;
        display: block;
    }

    .inform__text {
        font-size: 16px;
        font-weight: 400;
        text-align: center;
    }
}
