.header {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__wrap {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #2a2423;
}

.nav__bar {
    width: 100%;
    height: 41px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;  
}

.menu__list {
    margin: 0;
    padding: 0;
    list-style: none;
    position: absolute;
    top: -300px;
    right: 0;
    height: 300px;
    width: 100%;
    background: rgba(0,0,0,0.6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: .5s all;
    z-index: 98;
    
  }
  
.menu__item {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
.menu__link {
    text-decoration: none;
    color: #ffffff;
    font-size: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-weight: 300;
    transition: .5s;
  }
  
.menu__link:hover {
    color:  #c69c6d;
    transition: .5s;
  }

.burger__list {
    position: absolute;
    display: block;
    top: 15px;
    right: 30px;
    list-style: none;
    width: 16px;
    height: 14px;
    cursor: pointer;
    padding: 0;
    margin: 0;
    z-index: 99;
  }
  
.burger__item {
    height: 2px;
    width: 16px;
    background: #ffffff;
    position: absolute;
    transition: .7s all;
  }
.second, .third {
    top: 5px;
  }
  
.fourth {
    top: 10px;
  }
  
#check-menu {
    visibility: hidden;
  }
  
.menu__list--active {
    top: 0;
    transition: .5s all;
  }
  
.second--active {
    transform: rotate(45deg);
    background: #ffffff;
  }
  
.third--active {
    transform: rotate(-45deg);
    background: #ffffff;
  }

.header__logo-img {
      width: 80px;
      padding-left: 20px;
  }

.header__btn-phone {
      display: none;
  }
  
.phone-icon {
    position: relative;
    display: block;
    background-image: url("../img/icon-phone.png");
    width: 23px;
    height: 23px;
    margin-right: 10px;
}

.header__phone {
    width: 180px;
}

.phone {
    display: flex;
    align-items: center;
    font-size: 14px;
    text-decoration: none;
    color: #ffffff;
}

.header__block {
    width: 100%;
    background-color: #493a2f;
}

.header__block-left {
    background-image: url(../img/logo_white-02.png);
    background-repeat: no-repeat;
    background-size: auto;   
}

.header__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header__tittle {
    text-align: center;
}

.header__btn {
    width: 200px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    border: 1px solid #c69c6d;
    background-color: #c69c6d;
    border-radius: 5px;
    box-shadow: 1px 4px 26px 1px rgba(9, 10, 10, 0.05);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.32px;
    color: #ffffff;
    margin-bottom: 50px;
}

.header__block-right {
    display: none;
}

@media (min-width: 1440px){
    .header {
        width: 100%;
    }

    .nav__bar {
        width: 1265px;
        height: 81px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;  
    }

    .menu__bar {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }

    #check-menu {
        display: none;
    }

    .header__logo-img {
        width: 100%;
    }

    .burger__list {
        display: none;
    }

    .menu__list {
        position: unset;
        height: unset;
        width: unset;
        background: none;
        list-style: none;
        display: flex;
        flex-direction: row;
        margin: 0;
        padding: 0;
    }

    .menu__item {
        margin-right: 55px;
        
    }

    .menu__link {
        text-decoration: none;
        color: #e0e0e0;
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 0.49px;
        font-size: 14px;
    }

    .header__phone {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: unset;
    }

    .phone {
        display: flex;
        color: #e0e0e0;
        text-decoration: none;
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 0.84px;
        margin-right: 30px;
    }
    .phone-icon {
        position: relative;
        display: block;
        background-image: url("../img/icon-phone.png");
        width: 23px;
        height: 23px;
        margin-right: 10px;
    }

    .header__btn-phone {
        border: 2px solid #826f5a;
        border-radius: 3px;
        height: 35px;
        width: 144px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        text-transform: uppercase;
        letter-spacing: 0.42px;
        color: #baa287;
        text-decoration: none;
    }

    .header__block {
        height: 718px;
        display: flex;
    }

    .header__block-left {
        width: 50%;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        background-image: url(../img/logo_white-02.png);
        background-repeat: no-repeat;
        background-size: auto;   
        background-position: 0 -160px;    
    }

    .header__container {
        width: 635px;
        margin-top: 100px;
    }
    .header__tittle {
        width: 587px;
        color: #ffffff;
        font-size: 64px;
        font-weight: 400;
        font-family: "Bodoni";
        line-height: 77.59px;
        font-variant: small-caps;
        letter-spacing: 1.51px;
        margin-bottom: 50px;
        text-align: left;
    }

    .header__btn {
        width: 264px;
        height: 55px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        border: 1px solid #c69c6d;
        background-color: #c69c6d;
        border-radius: 5px;
        box-shadow: 1px 4px 26px 1px rgba(9, 10, 10, 0.05);
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.32px;
        color: #ffffff;
    }

    .header__block-right {
        width: 50%;
        height: 100%;
        display: block;
        position: relative;
        background-image: url(../img/фото_1.png);
        background-repeat: no-repeat;
        background-size: cover;
    }

}