.gallery {
    width: 100%;
}

.gallery__wrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-top: 40px;
    margin-bottom: 40px;
}

.gallery__tittle {
    font-family: "Bodoni";
    font-weight: 400;
    opacity: 0.8;
    font-size: 36px;
    letter-spacing: 1.1px;
    font-variant: small-caps;
    color: #493a2f;
    margin: 0;
}

.gallery__border {
    display: block;
    position: relative;
    background-color: #7a716c;
    height: 3px;
    width: 220px;
}

.owl-image {
    width: 320px;
    height: 214px;
}

.slick-prev, .slick-next, .slick-dots, .slick-next::before {
    display: none;
    right: 0;
}

@media (min-width:1440px){
    .main-page {
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    .gallery {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        overflow: hidden
    }

    .gallery__wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 400px;
        height: 124px;
        margin-top: 80px;
        margin-bottom: 50px;
    }

    .gallery__border {
        width: 364px;
    }

    .gallery__tittle {
        font-size: 48px;
    }

    .slick-slide {
        padding-left: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }

    .slick-prev, .slick-next  {
        position: absolute;
        display: block;
        width: 40px;
        height: 40px;
        //background: #ffffff;
        border-radius: 50%;
        z-index: 99;
    }

    .slick-prev {
        position: absolute;
        left: 47%;
    }

    .slick-prev, .slick-prev:hover, .slick-prev:focus, .slick-prev:active {
        background-image: url(../img/botton_left.png);
    }

    .slick-prev::before, .slick-next::before {
        display: none;
    }

    .slick-next, .slick-next:hover, .slick-next:focus, .slick-next:active {
        right: 47%;
        background-image: url(../img/botton_right.png);
    }

    .slick-dotted.slick-slider {
        margin-bottom: 0;
    }

    .owl-image {
        height: unset;
        width: unset;
    }

    .slick-dots {
        display: none;
    }
}