.reviews {
    width: 100%;
    background-image: url("../img/pexels-photo-1050304.png");
    background-repeat: no-repeat;
    background-position: 0 -180px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
    padding-bottom: 40px;
}

.reviews__wrap {
    margin-top: 50px;
    margin-bottom: 50px;
}

.reviews__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    display: none;
}

.reviews__gallery {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
@media (min-width: 768px){
    .reviews-item {
        display: none;
    }

    .reviews__list {
        display: flex;
    }

    .reviews__item {
        display: flex;
    }

    .reviews__item {
        margin: 8px 8px;
    }
}

@media (min-width: 1440px){
    .reviews {
        height: 980px;
        padding-bottom: 0;
    }

    .reviews__wrap {
        margin-top: 80px;
        margin-bottom: 60px;
    }

    .gallery__tittle {
        font-family: "Bodoni";
        font-size: 66px;
        letter-spacing: 1.51px;
    }

    .reviews__gallery {
        width: 760px;
        
    }

    .reviews__list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        margin: 0;
        
    }

    
}