@import "fonts.scss";

body {
  min-width: 320px;
  height: 100vh;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: sans-serif, "OpenSans";
  font-size: 14px;
  color: #ffffff;  
  background-repeat: no-repeat;
  background-size: auto;
}


.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

