.contakt {
    background-color: #493a2f;
    display: flex;
    justify-content: center;
}

.contakt__wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.contakt__block {
    width: 100%;
}

.contakt__tittle {
    width: 100%;
    font-weight: 400;
    font-family: "Bodoni";
    letter-spacing: 1.51px;
    text-transform: uppercase;
    font-size: 24px;
    margin-bottom: 20px;
    opacity: 0.8;
    text-align: center;
}

.contakt__description {
    opacity: 0.86;
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 30px;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
}

.contakt__border {
    margin: 0 auto;
}

.contakt__block-left {
    margin-top: 50px;
}

.form-control {
    width: 100%;
    height: 49px;
    background-color: #e9e9e9;
    border: none;
    border-radius: 10px;
    box-sizing: border-box;
    padding-left: 40px;
    margin-bottom: 20px;
}

textarea {
    background-color: #e9e9e9;
    width: 100%;
    height: 249px;
    border: none;
    border-radius: 10px;
    box-sizing: border-box;
    padding-left: 40px;
    padding-top: 40px;
}

.btn-primary {
    width: 100%;
    height: 49px;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 4px 26px 1px rgba(9, 10, 10, 0.05);
    background-color: #c69c6d;

    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.32px;
    color: #ffffff;
    font-size: 18px;
    margin-top: 20px;
}

.contakt__block-right {
    width: 100%;
}

.contakt__conteiner {
    background-color: #362f2d;
    border-radius: 10px;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h6 {
    opacity: 0.8;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.32px;
    padding-top: 70px;
    margin: 0;
}

.contakt__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    margin-left: 0;
    padding: 0;
}

.contakt__item {
    display: flex;
    flex-direction: row;
    margin-bottom: 40px;
    
}

.contakt__text {
    font-size: 16px;
    color: #ffffff;
    font-weight: 400;
    letter-spacing: 1.44px;
    opacity: 0.8;
    margin: 0;
    margin-left: 20px;
}

.contakt__item a {
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 1.44px;
    opacity: 0.8;
    color: #ffffff;
    text-decoration: none;
    margin-left: 20px;
}

svg {
    width: 30px;
    height: 30px;
    fill: #6d5b4b;
}

@media (min-width:1440px){
    .contakt {
        height: 978px;
    }

    .contakt__wrap {
        width: 1430px;
    }

    .contakt__tittle {
        width: 680px;
