    }

    .contakt__description {
        text-align: left;
        padding: 0;
    }

    .contakt__border {
        width: 132px;
        background-color: #7a716c;
        margin: 0;
    }

    .contakt__block {
        width: 100%;
    }

    .contakt__block-left, .contakt__block-right {
